.body {
  font-family: "Exo", sans-serif;
  font-family: "Open Sans", sans-serif;
  background: rgb(233, 242, 242);
  background: linear-gradient(
    0deg,
    rgba(233, 242, 242, 1) 0%,
    rgba(215, 201, 222, 0.9178265056022409) 100%
  );

  width: 100vw;
  overflow-x: hidden;
}

.exoFont {
  font-family: "Exo", sans-serif;
}

.openSans {
